var _self;
export default {
	name: 'change-img-tips',
	props: ['tipsType', 'value', 'tipsLeft'],
	data() {
		return {
			serverUploadSuccess: 'https://img01.iambuyer.com/imgup/upLoad/uploadSuccess',
			serverUrl: 'https://img01.iambuyer.com/imgup/upLoad/fileUpload',
			imageList: []
		};
	},
	methods: {
		clickShow() {
			this.$emit('show-Image');
		},
		// 替换图片
		changeImage: function() {
			console.log('触发替换');
			_self = this;
			uni.chooseImage({
				count: 1, // 最多可以选择的图片张数,这里设置了1张
				success: function(e) {
					console.log(e, '上传图片');
					var imagePathArr = e.tempFilePaths;

					//如果设置了limit限制，在web上count参数无效，这里做判断控制选择的数量是否合要求
					//在非微信小程序里，虽然可以选多张，但选择的结果会被截掉
					//在app里，会自动做选择数量的限制
					for (let i = 0; i < imagePathArr.length; i++) {
						_self.imageList.push(imagePathArr[i]);
					}
					//检查服务器地址是否设置，设置即表示图片要上传到服务器
					if (_self.serverUrl) {
						uni.uploadFile({
							url: _self.serverUrl,
							fileType: 'image',
							formData: _self.formData,
							filePath: imagePathArr[0],
							name: 'file',
							success: function(res) {
								if (res.statusCode === 200) {
									console.log(JSON.parse(res.data), '上传成功');
									uni.request({
										url: _self.serverUploadSuccess, //接口地址。
										data: { fileName: JSON.parse(res.data).name },
										method: 'POST',
										dataType: 'from-data',
										header: {
											'content-type': 'application/x-www-form-urlencoded'
										},
										success: function(res) {
											if (res.statusCode === 200) {
												console.log(JSON.parse(res.data), '上传成功');
												let imgUrl = JSON.parse(res.data).httpUrl;
												_self.$emit('change-Image', imgUrl);
											} else {
											}
										},
										fail: function(res) {}
									});
								} else {
								}
							},
							fail: function(res) {}
						});
					} else {
					}
				}
			});
		},
		// 添加图片
		addImage: function() {
			console.log('触发添加');
			_self = this;
			uni.chooseImage({
				count: 1, // 最多可以选择的图片张数,这里设置了1张
				success: function(e) {
					console.log(e, '上传图片');
					var imagePathArr = e.tempFilePaths;

					//如果设置了limit限制，在web上count参数无效，这里做判断控制选择的数量是否合要求
					//在非微信小程序里，虽然可以选多张，但选择的结果会被截掉
					//在app里，会自动做选择数量的限制
					for (let i = 0; i < imagePathArr.length; i++) {
						_self.imageList.push(imagePathArr[i]);
					}
					//检查服务器地址是否设置，设置即表示图片要上传到服务器
					if (_self.serverUrl) {
						uni.uploadFile({
							url: _self.serverUrl,
							fileType: 'image',
							formData: _self.formData,
							filePath: imagePathArr[0],
							name: 'file',
							success: function(res) {
								if (res.statusCode === 200) {
									console.log(JSON.parse(res.data), '上传成功');
									uni.request({
										url: _self.serverUploadSuccess, //接口地址。
										data: { fileName: JSON.parse(res.data).name },
										method: 'POST',
										dataType: 'from-data',
										header: {
											'content-type': 'application/x-www-form-urlencoded'
										},
										success: function(res) {
											if (res.statusCode === 200) {
												console.log(JSON.parse(res.data), '上传成功');
												let imgUrl = JSON.parse(res.data).httpUrl;
												_self.$emit('add-Image', imgUrl);
											} else {
											}
										},
										fail: function(res) {}
									});
								} else {
								}
							},
							fail: function(res) {}
						});
					} else {
					}
				}
			});
		},
		// 删除图片
		deleteImage: function() {
			console.log('触发删除');
			_self = this;
			_self.$emit('delete-Image');
		}
	}
};
